import axios from '../../axios-belwater-api'
import Vue from 'vue'

const session = Vue.prototype.$session

const state = {
  error: null,
  headers: null,
  items: null,
  loaded: false
}

const mutations = {
  setData (state, response) {
    if (response.err) {
      state.error = response.err
    } else if (response.items) {
      state.headers = response.headers
      state.items = response.items
      state.loaded = true
    }
  },
  clear (state) {
    state.error = null
    state.headers = null
    state.items = null
    state.loaded = false
  }
}

const actions = {
  getData ({ commit }, method) {
    commit('clear')
    axios.post('personal/get-data', { token: session.get('token'), method: method })
      .then(response => {
        commit('setData', response.data)
        commit('change_preloader', false, { root: true })
      })
      .catch(error => {
        commit('change_preloader', false, { root: true })
        console.log(error)
      })
  }
}

const getters = {
  error: () => state.error,
  headers: () => state.headers,
  items: () => state.items,
  loaded: () => state.loaded
}

export default {
  namespaced: true,
  state,
  mutations,
  actions,
  getters
}
