import axios from '../../axios-belwater-api'
import Vue from 'vue'

const session = Vue.prototype.$session

const state = {
  receipt: null,
  meteresInfo: null,
  metereInfo: null,
  meteres: null,
  error: null,
  flushMeteresResult: false
}

const mutations = {
  setReceipt (state, response) {
    if (response.err) {
      state.error = response.err
    } else if (response.receipt) {
      state.receipt = atob(response.receipt)
    }
  },
  setMeteresInfo (state, response) {
    if (response.err) {
      state.error = response.err
    } else if (response.data) {
      state.meteresInfo = response.data
    }
  },
  setMetereInfo (state, response) {
    if (response.err) {
      state.error = response.err
    } else if (response.data) {
      state.metereInfo = response.data
    }
  },
  setMeteres (state, response) {
    if (response.err) {
      state.error = response.err
    } else if (response.data) {
      state.meteres = response.data
    }
  },
  setFlushMeteresResult (state, response) {
    if (response.err) {
      state.error = response.err
    } else {
      state.flushMeteresResult = true
    }
  },
  clearError (state) {
    state.error = null
  }
}

const actions = {
  getReceipt ({ commit }) {
    commit('clearError')
    axios.post('personal/get-data', { token: session.get('token'), method: 'printReceipt' })
      .then(response => {
        commit('setReceipt', response.data)
        commit('change_preloader', false, { root: true })
      })
      .catch(error => {
        commit('change_preloader', false, { root: true })
        console.log(error)
      })
  },
  getMeteresInfo ({ commit }) {
    commit('clearError')
    axios.post('personal/get-data', { token: session.get('token'), method: 'meteresInfo' })
      .then(response => {
        commit('setMeteresInfo', response.data)
        commit('change_preloader', false, { root: true })
      })
      .catch(error => {
        commit('change_preloader', false, { root: true })
        console.log(error)
      })
  },
  getMetereInfo ({ commit }, data) {
    commit('clearError')
    axios.post('personal/get-data', { token: session.get('token'), params: data, method: 'metereInfo' })
      .then(response => {
        commit('setMetereInfo', response.data)
        commit('change_preloader', false, { root: true })
      })
      .catch(error => {
        commit('change_preloader', false, { root: true })
        console.log(error)
      })
  },
  getMeteres ({ commit }) {
    commit('clearError')
    axios.post('personal/get-data', { token: session.get('token'), method: 'meteres' })
      .then(response => {
        commit('setMeteres', response.data)
        commit('change_preloader', false, { root: true })
      })
      .catch(error => {
        commit('change_preloader', false, { root: true })
        console.log(error)
      })
  },
  flushMeteres ({ commit }, data) {
    commit('clearError')
    axios.post('personal/get-data', { token: session.get('token'), params: { meteres: data }, method: 'flushMeteres' })
      .then(response => {
        commit('setFlushMeteresResult', response.data)
        commit('change_preloader', false, { root: true })
      })
      .catch(error => {
        commit('change_preloader', false, { root: true })
        console.log(error)
      })
  }
}

const getters = {
  receipt: () => state.receipt,
  error: () => state.error,
  meteresInfo: () => state.meteresInfo,
  metereInfo: () => state.metereInfo,
  meteres: () => state.meteres,
  flushMeteresResult: () => state.flushMeteresResult
}

export default {
  namespaced: true,
  state,
  mutations,
  actions,
  getters
}
