import router from '../../router/index'
import axios from '../../axios-belwater-api'
import Vue from 'vue'

const session = Vue.prototype.$session

export default {
  state: {
    user: null
  },
  mutations: {
    setUser (state, user) {
      state.user = user
    },
    clear (state) {
      state.user = null
    }
  },
  actions: {
    storeUser ({ commit }, response) {
      if (!response.data.err && response.data.user) {
        session.start()
        session.set('token', response.data.user.auth_key)
        commit('setUser', response.data.user)
      }
    },
    signup ({ dispatch }, authData) {
      console.log(authData)
      axios.post('auth/signup', { SignupForm: authData })
        .then(response => {
          dispatch('storeUser', response)
          router.replace('/personal')
        })
        .catch(error => console.log(error))
    },
    login ({ dispatch }, authData) {
      axios.post('auth/login', {
        LoginForm: {
          email: authData.email,
          password: authData.password
        }
      })
        .then(response => {
          dispatch('storeUser', response)
        })
        .catch(error => console.log(error))
    },
    logout ({ commit }) {
      if (session.exists()) {
        commit('clear')
        session.destroy()
        router.replace('/')
      }
    },
    fetchUser ({ dispatch }) {
      if (session.exists()) {
        axios.post('auth/get-user', { token: session.get('token') })
          .then(response => {
            dispatch('storeUser', response)
          })
          .catch(error => console.log(error))
      }
    },
    tryAutoLogin ({ dispatch }) {
      dispatch('fetchUser')
    }
  },
  getters: {
    user (state) {
      return state.user
    },
    isAuthenticated (state) {
      return state.user !== null
    }
  }
}
