<template>
  <form class="mt-5">
    <v-text-field
      v-model="email"
      placeholder="Логин"
      color="white"
      solo
    ></v-text-field>
    <v-text-field
      v-model="password"
      placeholder="Пароль"
      color="white"
      solo
      type="password"
    ></v-text-field>
    <p v-if="error" style="color: red">Логин или пароль неверные</p>

    <v-btn class="mr-4 d-block" @click="onSubmit">Войти</v-btn>
    <router-link :to="{name: 'signup'}" class="white--text mt-3 d-block">Регистрация</router-link>
  </form>
</template>

<script>
export default {
  name: 'LeftLoginForm',
  data () {
    return {
      email: '',
      password: '',
      error: false
    }
  },
  methods: {
    onSubmit () {
      const formData = {
        email: this.email,
        password: this.password
      }
      this.$store.dispatch('login', formData)
      this.error = !this.$store.getters.isAuthenticated
    }
  }
}
</script>

<style scoped>
  .v-btn {
    text-transform: none;
  }
</style>
